'use strict';

// MODULES //
var PINF = __var_0__;
var NINF = __var_1__;
var isInt = __var_2__;


// MAIN //

/**
* Tests if a number primitive is an integer value.
*
* @private
* @param {*} value - value to test
* @returns {boolean} boolean indicating if a number primitive is an integer value
*/
function isInteger( value ) {
	return (
		value < PINF &&
		value > NINF &&
		isInt( value )
	);
}


// EXPORTS //
export default isInteger;
